.why-nxt {
  width: 100%;
}
.why-nxt-heading {
  flex-direction: column;
  gap: 1rem;
}
.why-nxt-grid {
  margin: 5rem 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 4.375rem;
  align-items: baseline;
}
.why-nxt-grid-1 {
  flex-direction: column;
  gap: 1.5rem;
}
.why-nxt-grid-1 img {
  width: 4.75rem;
  transition: all 0.5s;
  height: auto;
}
.why-nxt-grid-1 img:hover {
  transform: scale(1.1);
}
.why-nxt-grid-1 h3 {
  text-align: center;
  font-weight: 600;
  text-transform: capitalize;
}
.why-nxt-grid-1 p {
  color: #000;
  text-align: center;
  font-weight: 500;
  line-height: 1.6875rem;
  text-transform: capitalize;
}
@media (max-width: 1024px) {
  .why-nxt-grid {
    gap: 2.5rem;
  }
  .why-nxt-grid-1 h3 {
    font-size: 1.1rem;
  }
  .why-nxt-grid-1 p {
    font-size: 0.9rem;
    line-height: 1.3rem;
  }
  .why-nxt-grid-1 img {
    width: 4rem;
  }
}
@media (max-width: 768px) {
  .why-nxt-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .why-nxt-heading h2 {
    font-size: 2.5rem;
  }
}
@media (max-width: 425px) {
  .why-nxt-grid {
    grid-template-columns: repeat(1, 1fr);
  }
  .why-nxt-heading h2 {
    font-size: 2.2rem;
  }
}
/* BANNER  */
.banner {
