  border-radius: 0rem 12.5rem 0rem 0rem;
  background: #407bff;
}

.banner-grid-content {
}
.banner-grid-content h4 {
  color: #fff;
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 3.1875rem;
  text-transform: capitalize;
}
