/* NXT SKILLS POWER  */
.nxt-skills-power {
  position: relative;
  padding: 5rem 0;
  background: #0d0f11;
  width: 100%;
  color: white !important;
  z-index: 1;
}
.nxt-skills-power-heading {
  z-index: 1;
  padding: 0 2.5rem;
}
.nxt-power-background-line {
  position: absolute;
  right: 0px;
  left: 0px;
  z-index: -1;
}
.nxt-power-background-line img {
  width: 100%;
}
.nxt-skills-power-heading h2 {
  color: #404243;
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  text-transform: capitalize;
}
.nxt-skills-power-main-grid {
  gap: 3rem;
  justify-content: space-around;
  position: relative;
  z-index: 1;
}
.nxt-skills-power-ball {
  position: absolute;
  height: 8rem;
  width: 8rem;
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #424242 100%);
  border-radius: 50%;
  top: 0px;
  right: -450px;
  transition: all 0.8s;
  z-index: -1;
  animation: bounce 0.5s;
  animation-direction: alternate;
  animation-timing-function: cubic-bezier(0.5, 0.05, 1, 0.5);
  animation-iteration-count: infinite;
}
.nxt-skills-power-ball-1-1 {
  position: absolute;
  height: 8rem;
  width: 8rem;
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #424242 100%);
  border-radius: 50%;
  top: 0px;
  right: -450px;
  transition: all 0.8s;
  z-index: -1;
  animation: bounce 0.5s;
  animation-direction: alternate;
  animation-timing-function: cubic-bezier(0.5, 0.05, 1, 0.5);
  animation-iteration-count: infinite;
}
@keyframes bounce {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, 200px, 0);
  }
}
.same-next-power-ball {
  position: absolute;
  height: 1rem;
  width: 1rem;
  top: 0px;
  right: 0px;
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #424242 100%);
  border-radius: 50%;
  animation: bounce 0.7s;
  animation-direction: alternate;
  animation-timing-function: cubic-bezier(0.5, 0.05, 1, 0.5);
  animation-iteration-count: infinite;
  z-index: -1;
}
.nxt-skills-power-ball-2 {
  top: 0px;
  right: 600px;
}
.nxt-skills-power-sub-gird {
  display: grid;
  grid-template-columns: repeat(3, 168px);
  grid-template-rows: repeat(10, 55px);
  justify-content: left;
  margin: 6rem 0;
}
.power-same-grid {
  background-size: cover;
  background-position: center;
  transition: all 0.5s;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
.power-same-grid p {
  text-align: center;
  font-size: 0.8125rem;
  font-weight: 700;
  text-transform: capitalize;
  padding: 0 2.5rem;
  margin-top: -3rem;
}
.power-sub-gird-2 {
  grid-area: 1 / 2/ 5 / 3;
  background-image: url("../Images/NXT\ Power/hex.svg");
}
.power-sub-gird-8 {
  grid-area: 4 / 1/ 8 / 2;
  background-image: url("../Images/NXT\ Power/hex.svg");
}
.power-sub-gird-9 {
  grid-area: 4 / 3/ 8 / 4;
  background-image: url("../Images/NXT\ Power/hex.svg");
}
.power-sub-gird-12 {
  grid-area: 7 / 2/ 11 / 3;
  background-image: url("../Images/NXT\ Power/hex.svg");
}
.power-sub-gird-2-ball {
  top: 116px;
  right: 188px;
  filter: drop-shadow(0px 0px 50px rgba(231, 46, 111, 0.5));
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #e72e6f 100%);
}
.power-sub-gird-8-ball {
  top: 283px;
  right: 356px;
  filter: drop-shadow(0px 0px 50px rgba(231, 46, 111, 0.5));
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #e72e6f 100%);
}
.power-sub-gird-9-ball {
  top: 283px;
  right: 20px;
  filter: drop-shadow(0px 0px 50px rgba(231, 46, 111, 0.5));
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #e72e6f 100%);
}
.power-sub-gird-12-ball {
  top: 448px;
  right: 190px;
  filter: drop-shadow(0px 0px 50px rgba(231, 46, 111, 0.5));
  background: radial-gradient(50% 50% at 50% 50%, #fefefe 0%, #e72e6f 100%);
}
.power-sub-gird-12:hover {
  transform: scale(0.9);
}
.power-sub-gird-9:hover {
  transform: scale(0.9);
}
.power-sub-gird-8:hover {
  transform: scale(0.9);
}
.power-sub-gird-2:hover {
  transform: scale(0.9);
}
.nxt-skills-power-gird-2 {
  width: 31rem;
  margin-top: 180px;
}
.nxt-skills-power-gird-2-detail h4 {
  font-size: 1.25rem;
  font-weight: 600;
  text-transform: capitalize;
}
.nxt-skills-power-gird-2-detail h3 {
  color: white;
  padding: 2.5rem 0;
  font-size: 1.875rem;
  font-weight: 600;
  text-transform: capitalize;
}
.nxt-skills-power-gird-2-detail p {
  font-family: "Poppins";
  font-size: 1rem;
  font-weight: 500;
  line-height: 1.875rem;
  text-transform: capitalize;
}
.nxt-power-hidden-text-1-main {
  padding: 3rem 0 0 0;
}
.nxt-power-hidden-text-same {
  display: none;
}
.nxt-power-hidden-text-1-p {
  color: #b6b6b6;
  font-size: 1rem;
  font-weight: 600;
  line-height: 2.0625rem;
  text-transform: capitalize;
}
.nxt-power-hidden-text-1-p span {
  color: white;
  font-weight: 800 !important;
}
.nxt-power-hidden-text-1 {
  display: flex;
}
.nxt-power-hidden-text-1-hide {
  display: none;
}
.nxt-power-hidden-text-2,
.nxt-power-hidden-text-3,
.nxt-power-hidden-text-4 {
