}
@media (max-width: 1100px) {
  .nxt-skills-power-sub-gird {
    grid-template-columns: repeat(3, 136.1px);
    grid-template-rows: repeat(10, 45px);
  }
  .power-same-grid p {
    font-size: 0.7rem;
  }
  .nxt-skills-power-gird-2-detail h3 {
    font-size: 1.5rem;
    padding: 1.8rem 0;
  }
  .nxt-skills-power-gird-2-detail h4 {
    font-size: 1.1rem;
  }
  .nxt-skills-power-gird-2-detail p {
    font-size: 0.85rem;
  }
  .nxt-skills-power-gird-2 {
    width: 100%;
    margin-top: 5.625rem;
  }
  .nxt-power-hidden-text-1-main {
    padding: 2rem 0 0 0;
  }
  .nxt-skills-power-heading h2 {
    font-size: 2.5rem;
  }
  .nxt-skills-power-ball {
    right: -400px;
    width: 6.125rem;
    height: 6.125rem;
  }
  .power-sub-gird-2-ball {
    top: 120px;
    right: 155px;
  }
  .power-sub-gird-8-ball {
    top: 250px;
    right: 290px;
  }
  .power-sub-gird-9-ball {
    top: 256px;
    right: 20px;
  }
  .power-sub-gird-12-ball {
    top: 388px;
    right: 155px;
  }
  @keyframes bounce {
    from {
      transform: translate3d(0, 0, 0);
    }
    to {
      transform: translate3d(0, 150px, 0);
    }
  }
}
/* (Responsive power) */
.nxt-power-responsive-in-power {
  display: none;
}
.nxt-power-responsive {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2.5rem;
}
.nxt-power-responsive-grid {
  flex-direction: column;
  border-radius: 0.4375rem;
  background: #1e1e1e;
  box-shadow: 8px 8px 0px 0px #000;
  padding: 1.5rem 1rem;
  transition: all 0.5s;
}
.nxt-power-responsive-grid h4 {
  text-align: center;
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: capitalize;
}
.nxt-power-responsive-grid hr {
  background: #fff;
  height: 4px;
  width: 30%;
  border-radius: 40%;
  margin: 1.3rem 0;
}
.nxt-power-responsive-grid p {
  color: #b6b6b6;
  text-align: center;
  font-size: 0.8125rem;
  font-weight: 500;
  line-height: 1.25rem;
  text-transform: capitalize;
}
.nxt-power-responsive-grid:hover {
  box-shadow: none;
}

@media (max-width: 900px) {
  .nxt-skills-power-main-grid {
    gap: 0;
    flex-direction: column;
  }
  .nxt-skills-power-ball {
    display: none;
  }
  .nxt-skills-power-sub-gird {
    margin-bottom: 0;
  }
  .nxt-skills-power-gird-2 {
    display: flex;
    margin-top: 2.5rem;
    text-align: center;
  }
  .nxt-skills-power-sub-gird {
    margin-top: 3rem;
  }
}
.nxt-skills-power-gird-1 {
  position: relative;
}
@media (max-width: 500px) {
  .nxt-power-responsive-in-power {
    display: inline-block;
  }
  .nxt-skills-power-main-grid {
    gap: 3rem;
  }
  .nxt-skills-power-gird-1 {
    display: none;
  }
  .nxt-power-hidden-text-1-main {
    display: none;
  }
  .nxt-skills-power-heading h2 {
    font-size: 1.5rem;
